import * as d3 from "d3"
import { useMemo } from "react"

const MARGIN = { top: 10, right: 10, bottom: 30, left: 30 };

export default function Heatmap(props) {

    const boundsWidth = props.width - MARGIN.right - MARGIN.left;
    const boundsHeight = props.height - MARGIN.top - MARGIN.bottom;

    const yAxisElements = useMemo(() => [...new Set(props.data.map((d) => d.resource_type))], [props.data])
    const xAxisElements = useMemo(() => [...new Set(props.data.map((d) => d.time))], [props.data])

    const yScale = useMemo(() => {
        return d3
          .scaleBand()
          .range([0, boundsWidth])
          .domain(yAxisElements)
          .padding(0.01);
    }, [props.data, props.width]);

    const xScale = useMemo(() => {
        return d3
          .scaleBand()
          .range([0, boundsWidth])
          .domain(xAxisElements)
          .padding(0.01);
    }, [props.data, props.width]);
    
    const yLabels = yAxisElements.map((name, i) => {
    const yPos = yScale(name) ?? 0;
    return (
        <text
        key={i}
        x={-5}
        y={yPos + yScale.bandwidth() / 2}
        textAnchor="end"
        dominantBaseline="middle"
        fontSize={10}
        >
        {name}
        </text>
    );
    });

    const xLabels = xAxisElements.map((name, i) => {
    const xPos = xScale(name) ?? 0;
    return (
        <text
        key={i}
        x={xPos + xScale.bandwidth() / 2}
        y={boundsHeight + 10}
        textAnchor="middle"
        dominantBaseline="middle"
        fontSize={10}
        >
        {name}
        </text>
    );
    });

    const colorScale = d3
        .scaleSequential()
        .interpolator(d3.interpolateRdYlGn)
        .domain([10, 0]); // make sure to set min max  later lol

    const allRects = props.data.map((d, i) => {
        if (d.value === null) {
            return;
        }
        return (
            <rect
            key={i}
            x={xScale(d.time)}
            y={yScale(d.resource_type)}
            width={xScale.bandwidth()}
            height={yScale.bandwidth()}
            fill={colorScale(d.value)}
            />
        );
    });

    return (
        <div>
            <svg width={props.width} height={props.height}>
                <g
                    width={boundsWidth}
                    height={boundsHeight}
                    transform={`translate(${[MARGIN.left, MARGIN.top].join(",")})`}
                >
                {allRects}
                {xLabels}
                {yLabels}
                </g>
            </svg>
        </div>
    )
}

